import React from "react";
import {Collapse} from 'react-collapse';
import './collapseBox.css';

class ButtonCollapse extends React.Component {

    constructor(props) {
      super(props);
      this.state = {collpased: false};
    }

    onClicky(linkToOpen) {
      this.setState({
        collpased: !this.state.collpased
    });
    }

    render() {
      const divsToDisplay = [];
      for (const div in this.props.textsInCollapse)
      {
        divsToDisplay.push(<div>{this.props.textsInCollapse[div]}</div>);
      }


      return <div>
      <p>
      {this.props.text}
    </p>
    <br></br>
    <button
      onClick={e => this.onClicky(this.props.link)}
       >
      {this.props.buttonText}
    </button>
    <Collapse isOpened={this.state.collpased}>
      <div className="collapseBox">
        {divsToDisplay}
      </div>
    </Collapse>
    </div>;
    }
  }

export default ButtonCollapse;