import React from "react";
import birdyLeft from "../assets/images/svgs/pollo.svg";
import birdyRight from "../assets/images/svgs/polla2.svg";
import heart from "../assets/images/svgs/descargados/heart.svg";
import './electrochicken.css';

class ElectroChickeAnimation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {initialAnimationCompleted:false};
  }
  componentDidMount() {
    this.timerID = setInterval(
        () => this.tick(),
        10000
      );
  }

  componentWillUnmount() {
      clearInterval(this.timerID);
  }

  tick() {
    clearInterval(this.timerID);
      this.setState({
        initialAnimationCompleted: true
      });
  }


    render() {
      const heartElement = this.state.initialAnimationCompleted ?
        <img id="heartsLoop" src={heart}/> :
        <img id="hearts" src={heart} />;

      return  (
      <div id="animationContainer">
        <div id="heartContainer">
        {heartElement}
          </div>
        <div id="overflowContainer">
        <img
          id="left"
          src={birdyRight}/>
        <div className="betweenSpace"/>
        <img
          id="right"
          src={birdyLeft}></img>
      </div>

    </div>

    );
    }
  }

export default ElectroChickeAnimation;