import React from "react";
class ContentWithLink extends React.Component {

    render() {
      function onClicky(linkToOpen) {
        window.open(linkToOpen);
      }

      return <div>
      <p>
      {this.props.text}
    </p>
    <br></br>
    <button
      onClick={e => onClicky(this.props.link)}
       >
      {this.props.buttonText}
    </button>
    </div>;
    }
  }

export default ContentWithLink;